<template>
  <div id="Session">
    <div v-if="editable">
      <v-text-field
        v-model="data.Name"
        v-on:keyup.enter="editable = !editable"
        dense
        autofocus
      ></v-text-field>
      <v-text-field
        v-model="data.Des"
        v-on:keyup.enter="editable = !editable"
        dense
      ></v-text-field>
      <v-btn color="primary" @click="editable = !editable" block>
        <v-icon>mdi-check</v-icon>
      </v-btn>
    </div>
    <div v-if="!editable" v-on:click="editable = !editable">
      <div
        align="center"
        class="blue lighten-4 pa-2 mb-2 font-weight-bold rounded blue--text text--darken-4"
        v-text="data.Name"
      ></div>
      <div v-text="data.Des"></div>
    </div>
    <draggable
      element="span"
      v-model="data.Question"
      v-bind="dragOptions"
      :group="{ name: 'items' }"
      ghost-class="ghost"
      @start="dragging = true"
      @end="dragging = false"
      handle=".move"
      :sort="true"
    >
      <transition-group type="transition" tag="v-list">
        <v-hover open-delay="0"
          v-slot:default="{ hover }" v-for="(element, i) in data.Question"
          :key="i">
          <v-card
            :ripple="false"
            flat
            outlined
            class="mb-3 hold"
            :class="{ 'on-hover': hover }"
          >
          <v-toolbar style="max-height: 50px;" dense flat class="move" :color="hover?'grey lighten-2':''">
            <v-row>
              <v-col class="drag" cols="2"></v-col>
              <v-col class="d-flex justify-space-around drag">
                <v-icon>mdi-drag</v-icon>
              </v-col>
              <v-col cols="2" class="d-flex justify-end">
                <v-btn
                  class="mt-2"
                  text
                  icon
                  color="gray"
                  v-on:click="data.Question.splice(i, 1)"
                >
                  <v-icon>mdi-close</v-icon>
                </v-btn>
              </v-col>
            </v-row>
          </v-toolbar>
          <div class="px-3">
            <component
            :is="element.Type"
            :data="element"
            :key="genQID(element)"
          ></component>
          </div>
        </v-card>
        </v-hover>
      </transition-group>
    </draggable>
  </div>
</template>

<script>
import Radio from "./Radio.vue";
import Checkbox from "./CheckBox"
import TextBox from "./TextBox.vue";
import TextArea from "./TextArea.vue";
import MarkTable from "./MarkTable.vue";
import FileInput from "./FileInput.vue";
import draggable from "vuedraggable";


export default {
  name: "Session",
  props: {
    data: {
      QID: String,
      Name: String,
      Des: String,
      Question: Array,
      dragging: false,
    },
  },
  data: function () {
    return {
      editable: false,
    };
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
  methods: {
    genQID(e) {
      const key = Math.floor(Math.random() * (1000000 - 1)) + 100000;
      if (e.QID) return e.QID;
      else if (e.Type === 'MarkTable') return e.Type;
      else if (e.Type === 'Checkbox') {
        this.$set(e, "GroupID", key);
      } else {
        this.$set(e, "QID", key);
      }
      return e.QID;
    },
  },
  components: {
    draggable,
    Checkbox,
    TextBox,
    Radio,
    FileInput,
    MarkTable,
    TextArea,
  },
};
</script>
<style scoped>
.flip-list-move {
  transition: transform 0.5s;
}
.no-move {
  transition: transform 0s;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}
.list-group {
  min-height: 20px;
}
.list-group-item {
  cursor: move;
}
.list-group-item i {
  cursor: pointer;
}
.hold:hover {
  /* border: 2px solid rgba(128, 128, 128, 0.425); */
  outline: 2px solid rgba(128, 128, 128, 0.425);
}
.drag{
  cursor: grab;
}
</style>