<template>
  <div id="app">
    <v-app>
      <v-main>
        <v-row class="my-1" justify="center" no-gutters>
          <v-col
            class="px-1"
            style="max-width: 150px !important"
            align="center"
            outlined
            tile
            dense
            fixed
          >
            <v-card tile>
              <v-toolbar flat dense color="blue darken-4" dark>
                <span class="title font-weight-regular">Control</span>
              </v-toolbar>
              <draggable
                element="span"
                v-model="controls"
                :sort="false"
                :group="{ name: 'items', pull: 'clone', put: false }"
                :clone="handleClone"
              >
                <transition-group
                  class="list-group overflow-y-auto"
                  three-line
                  style="max-height: calc(100vh - 100px)"
                  tag="v-list"
                >
                  <v-list-item
                    class="list-group-item grab"
                    v-for="element in controls"
                    :key="uuid(element)"
                    justify="center"
                  >
                    <v-list-item-title
                      class="d-flex justify-start"
                      :class="
                        element.fixed
                          ? 'fa fa-anchor'
                          : 'glyphicon glyphicon-pushpin'
                      "
                      @click="element.fixed = !element.fixed"
                      v-text="element.Type"
                      aria-hidden="true"
                    ></v-list-item-title>
                  </v-list-item>
                </transition-group>
              </draggable>
              <v-btn tile color="blue" outlined block>Thêm</v-btn>
            </v-card>
          </v-col>
          <v-col style="max-width: 640px !important" outlined tile>
            <v-toolbar flat dense color="blue darken-4" dark>
              <!-- <v-text-field
                v-model="nameTemplates"
                solo hide-details single-line></v-text-field> -->
              <!-- <v-text-field background-color="#00000055" loading="inputName.isChange" style="max-width: 200px;" @change="inputName.isChange = true " flat solo dense hide-details single-line placeholder="Tên mẫu"/> -->
              <v-text-field background-color="#00000000" v-model="nameTemplates" loading="false" style="max-width: 200px;" dense flat solo hide-details single-line placeholder="Tên mẫu"/>
              <v-spacer></v-spacer>
              <v-file-input style="max-width: 30px" accept="application/json" prepend-icon="mdi-import" hide-input @change="importTemplates"></v-file-input>
              <v-btn icon @click="export2txt">
                <v-icon>mdi-download</v-icon>
              </v-btn>
            </v-toolbar>
            <v-card
              class="pa-5 overflow-y-auto"
              three-line
              style="max-height: calc(100vh - 100px)"
            >
              <div class="mx-2">
                <formtitle :data="templates"></formtitle>

                <draggable
                  v-model="templates.Part"
                  element="span"
                  :group="{ name: 'part' }"
                  :sort="true"
                  handle=".dragbar"
                >
                  <v-card
                    class="px-3 mt-2"
                    flat
                    outlined
                    v-for="(element, i) in templates.Part"
                    :key="i"
                  >
                    <v-toolbar flat tile class="dragbar" align="right">
                      <v-row>
                        <v-col cols="2"></v-col>
                        <v-col class="d-flex justify-space-around">
                          <!-- <v-icon>mdi-drag</v-icon> -->
                        </v-col>
                        <v-col cols="2" class="d-flex justify-end">
                          <v-btn
                            class="my-2 mx-0"
                            text
                            icon
                            color="red"
                            v-on:click="templates.Part.splice(i, 1)"
                          >
                            <v-icon>mdi-delete</v-icon>
                          </v-btn>
                        </v-col>
                      </v-row>
                    </v-toolbar>
                    <session :data="element" :key="i"></session>
                  </v-card>
                </draggable>
                <div class="mt-2">
                  <v-btn
                    v-on:click="
                      templates.Part.push({
                        Name: 'THÔNG TIN',
                        Des: 'Mô tả',
                        Question: [],
                      })
                    "
                    color="blue"
                    outlined
                    block
                    >add session</v-btn
                  >
                </div>
              </div>
            </v-card>
          </v-col>
        </v-row>
        <v-row no-gutters>
          <v-col class="pa-2" outlined tile>
            <pre>{{ listString }}</pre>
          </v-col>
        </v-row>
      </v-main>
    </v-app>
  </div>
</template>

<script>
import session from "./components/Session";
import formtitle from "./components/Formtitle";
import draggable from "vuedraggable";

export default {
  name: "App",
  data: function () {
    return {
      nameTemplates: "qaform",
      controls: [
        {
          QID: "",
          Type: "TextBox",
          Text: "Tiêu đề",
          Holder: "Nhập câu hỏi",
        },
        {
          QID: "",
          Type: "FileInput",
          Text: "Tiêu đề",
        },
        {
          QID: "",
          Type: "TextArea",
          Text: "Tiêu đề",
          Row: "3",
          Holder: "Nhập nội dung",
        },
        {
          QID: "",
          Type: "Radio",
          Text: "Tiêu đề",
          Option: [
            {
              Text: "Tuỳ chọn 1",
            },
            {
              Text: "Tuỳ chọn 2",
            },
          ],
        },
        {
          GroupID: this.generateUUID(),
          Type: "Checkbox",
          Text: "Tiêu đề CheckBox",
          Option: [
            {
              QID: this.generateUUID(),
              Text: "Checkbox 1",
            },
          ],
        },
        {
          Type: "MarkTable",
          Text: "Tiêu đề MarkTable",
          Des: "Mô tả",
          ValueArray: [
            {
              Value: "1",
              Text: "Rất thấp",
            },
            {
              Value: "2",
              Text: "Thấp",
            },
            {
              Value: "3",
              Text: "Trung bình",
            },
            {
              Value: "4",
              Text: "Cao",
            },
            {
              Value: "5",
              Text: "Rất cao",
            },
          ],
          Topic: [
            {
              GroupID: this.generateUUID(),
              Name: "Tiêu đề câu hỏi",
              Question: [
                {
                  QID: this.generateUUID(),
                  Text:
                    "1. Câu hỏi",
                },
              ],
            },
          ],
        },
      ],
      templates: {
        "templateID": this.generateUUID(),
        "logo_org": "https://lhu.edu.vn/ViewPage/LHUVN/_default/image/truong_dai_hoc_lac_hong_logo.png",
        "name_org": "Trường Đại học Lạc Hồng",
        "url_finishRedirect": "https://lhu.edu.vn",
        "Title": "PHIẾU KHẢO SÁT",
        "Des": "Nhằm mục đích đem đến những giá trị tốt đẹp nhất cho các bạn sinh viên. Trường Đại học Lạc Hồng rất mong các Bạn sinh viên đóng góp một số ý kiến cá nhân của bản thân. Mọi thông tin trong phiếu khảo sát sẽ được lưu trữ mang tính chất bảo mật và không công khai.",
        "url_login": "",
        "url_template": "",
        "Part": [
          {
            "Name": "THÔNG TIN CHUNG",
            "Des": "",
            "Question": []
          }
        ]
      }
    };
  },
  methods: {
    importTemplates(files) {
      
      if (files.length <= 0) {
        return false
      }
      const fr = new FileReader();
      fr.onload = e => {
        const result = JSON.parse(e.target.result);
        this.templates = JSON.parse(JSON.stringify(result, null, 2));
      }
      fr.readAsText(files);
    },
    export2txt() {
      const originalData = this.templates;
      const a = document.createElement("a");
      a.href = URL.createObjectURL(new Blob([JSON.stringify(originalData, null, 2)], {
        type: "application/json"
      }));
      a.setAttribute("download", this.nameTemplates + ".json");
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
    },
    uuid(e) {
      if (e.uid) return e.uid;
      const key = Math.random().toString(16).slice(2);
      this.$set(e, "uid", key);
      return e.uid;
    },
    generateUUID() {
      const key = Math.floor(Math.random() * (1000000 - 1)) + 100000;
      return key;
    },
    handleClone(item) {
      // Create a fresh copy of item
      let cloneMe = JSON.parse(JSON.stringify(item));
      this.$delete(cloneMe, "uid");
      return cloneMe;
    },
  },
  components: {
    draggable,
    session,
    formtitle,
  },
  computed: {
    listString() {
      return JSON.stringify(this.templates, null, 2);
    },
  },
};
</script>

