<script>
import draggable from "vuedraggable";

export default {
  name: "marktable",
  props: {
    data: {
      Text: String,
      Des: String,
      ValueArray: Array,
      Topic: Array,
    },
  },
  data: function () {
    return {
      editable: false,
      edititem: {},
    };
  },
  methods: {
    genQID() {
      const key = Math.floor(Math.random() * (1000000 - 1)) + 100000;
      return key;
    },
    focusable(e) {
      e.target.select();
    },
  },
  components: {
    draggable,
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
};
</script>
